import React from 'react';
import Helmet from 'react-helmet';
import { View } from '@rk/design-system';
import Blog from './Blog';
import Nav from './Nav';

const BlogPage = ({
  title,
  header,
  subheader,
  metadata,
  children,
  titleLinkHandler,
}) => {
  return (
    <>
      {title && <Helmet title={`${title} | ${metadata.title}`} />}
      <div class="container">
        <Nav />
        <Blog
          metadata={metadata}
          header={header}
          subheader={subheader}
          children={children}
        />
      </div>
      <View
        print="none"
        py={'2.5rem'}
        textAlign="center"
        css={`
          background: ${(props) => props.theme.light.colors.secondary};

          @media (prefers-color-scheme: dark) {
            background: ${(props) => props.theme.dark.colors.secondary};
          }
        `}
      >
        &copy; {metadata.year} by {metadata.author.name}
        . All rights reserved. <br />
      </View>
    </>
  );
};

export default BlogPage;
