import React from 'react';
import Helmet from 'react-helmet';
import { View, Sidebar } from '@rk/design-system';
import { default as SidebarContent } from './Sidebar';
import Main from './Main';
import Nav from './Nav';

const Page = ({
  title,
  header,
  subheader,
  metadata,
  children,
  titleLinkHandler,
}) => {
  return (
    <>
      {title && <Helmet title={`${title} | ${metadata.title}`} />}
      <Sidebar
        className="linear-bg"
        width={280}
        sidebar={
          <View mt={[0, 0, 'auto']} mx={['auto', 'auto', 0]} p={'1rem'}>
            <SidebarContent
              metadata={metadata}
              titleLinkHandler={titleLinkHandler}
            />
          </View>
        }
      >
        <>
          <div class="container">
            <Main
              metadata={metadata}
              header={header}
              subheader={subheader}
              children={children}
            />
          </div>
          <View
            print="none"
            py={'2.5rem'}
            textAlign="center"
            css={`
              background: ${(props) => props.theme.light.colors.secondary};

              @media (prefers-color-scheme: dark) {
                background: ${(props) => props.theme.dark.colors.secondary};
              }
            `}
          >
            &copy; {metadata.year} by {metadata.author.name}
            . All rights reserved. <br />
          </View>
        </>
      </Sidebar>
    </>
  );
};

export default Page;
